.skill-container{
    position: relative;
    margin-top: 15rem;
    display: grid;
    gap: 2rem;
    justify-content: space-evenly;
}

.skill-container h2{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 1rem;
}

.skills-logos{
    display: grid;
    gap: 6rem;
    grid-template-columns: repeat(4,1fr);
}


.skill-icons img{
    width: 70px;
}

@media (width < 650px){
    .skills-logos{
        display: grid;
        grid-template-columns: repeat(2,1fr);
    }
}